import React, {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import axios from "axios";


function GroupDetail({match}) {

    const [group, setGroup] = useState({})
    const [users, setUsers] = useEffect([])
    const id = match.params.id

    useEffect(() => {
        axios({
            method: "GET",
            url: `http://localhost:8000/api/groups/${id}`
        }).then(response => {
            setGroup(response.data)
            setUsers(response.data.users)
        })
    }, [id])

    return (
        <div>
            Group with id {group.id}
            <p>Group: <strong>{group.description}</strong></p>
            <hr/>
            <div class="row">
                {users.map(u => (
                    <div className="col-md-4" key={u.id}>
                        <h4>{u.name}</h4>
                        <p>{u.description}</p>
                        <p>{u.id}</p>
                        <Link to={{pathname: `/users/${u.id}`, fromDashboard: false}}>Details</Link>
                    </div>
                ))}
            </div>
        </div>
    )
}

export default GroupDetail;
